

*{
    font-family: 'Montserrat', sans-serif;
}

.app-nav{
    padding-bottom: 12px;
}



body{
    background : -webkit-linear-gradient(rgba(0,0,0,0.8),rgba(0,0,0,0.8)),url(/website/static/media/bg.b1d7c44c.jpg);
    background : linear-gradient(rgba(0,0,0,0.8),rgba(0,0,0,0.8)),url(/website/static/media/bg.b1d7c44c.jpg);
    height : 100vh !important;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
}

.navbar-custom{
    background: rgba(0,0,0,0.5);
}

#name{
    font-size: 70px;
    font-weight: 100;
    letter-spacing: -1px;
}

@media(max-width : 768px){
    #name{
        font-size: 50px;
        text-align: center;
    }
    #para{
        text-align: justify;
    }
    #my-btn{
        text-align: center;
        padding-top: 50px;
    }
    #quem{
        padding-top: 30px;
        text-align: justify;
    }
    #skill_desc{
        text-align: justify;
    }
    #second{
        margin-top: 400px;
    }
    #mob{
        margin-top: -50px;
    }
}
#location{
    padding : 0;
    margin : 0;
}

.btn-custom{
    background: #28a745;
    color : white
}

.btn-custom:hover{
    background: darkgreen;
    color : white ;
    
}
#profession{
    border-top : 1px solid #ccc;
    width : 320px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 5px;
    padding-top: 5px;
}

#myabout{
    height : 100vh;
}

#myabout p{
    color : #ccc
}
#myabout h1{
    color : #ccc
}



